<style lang="scss" scope>
.ui-page{
    padding-bottom: 20px;
    .ui-list{
        min-height: 300px;
        margin-bottom: 30px;
        .ui-detail-box{
            justify-content: space-around;
            flex-wrap: wrap;
            padding: 40px 60px;
            .func_item{
                width: 120px;
                height: 120px;
                padding-top: 20px;
                color: #304156;
                font-weight: bold;
                transition: all 0.6s;
                border-radius: 5px;
                cursor: pointer;
                .box_icon{
                    font-size: 50px;
                    
                }
            }
            .func_item:hover{
                background-color: #304156;
                color: #E1E2E7;
                transform: translate(0,-10px);
            }
            
        }
    }
    .reward_status{
        padding: 2px;
        border-radius: 3px;
        color: #67C23A;
    }
    .leave{
        justify-content: space-between;
        
    }
    .leave_dialog{
        box-sizing: border-box;
        padding: 0 20px;
        .left_form{
            color: black;
            .leave_item_reset{
                width: 220px;
            }
        }
    }
}
.ui-list-header{
    padding:0 20px;
}
.echart_punch{
    width: 400px;
    height: 400px;
}
</style>
<template>
    <div class="ui-page">
        <div class="ui-list ui-box">
            <div class="box-header ui-list-header">
                <div>常用功能</div>
                <div>
                    <el-button type="primary" size="mini" @click="back">返回</el-button>
                </div>
            </div>
            <div class="item_row ui-detail-box">
                <div class="item_column func_item" @click="leaveDialogVisible=true">
                    <i class="el-icon-remove box_icon"></i>
                    我要请假
                </div>
                <div class="item_column func_item" @click="toDiv('#reward_box')">
                    <i class="el-icon-s-cooperation box_icon"></i>
                    查看奖惩
                </div>
                <div class="item_column func_item" @click="toDiv('#person_box')">
                    <i class="el-icon-user-solid box_icon"></i>
                    个人信息
                </div>
            </div>
        </div> 
        <div class="ui-list ui-box">
            <div class="box-header ui-list-header">
                <div>本月打卡情况</div>
            </div>
            <div class="item_row ui-detail-box">
                <div class="echart_punch" ref="echart_punch"></div>
                <div class="echart_punch" ref="echart_mission"></div>
            </div>
        </div> 
        <div class="ui-list ui-box">
            <div class="box-header ui-list-header">
                <div>请假记录</div>
                <div>
                    <el-button size="mini">刷新</el-button>
                    <el-button type="primary" size="mini">导出</el-button>
                </div>
            </div>
            <el-table :data="leaveList" :cell-style="cellStyle" :header-cell-style="rowClass">
                <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column prop="name" label="姓名"></el-table-column>
                <el-table-column prop="create_time" label="申请时间" show-overflow-tooltip></el-table-column>
                <el-table-column prop="start_time" label="开始时间" show-overflow-tooltip></el-table-column>
                <el-table-column prop="end_time" label="结束时间" show-overflow-tooltip></el-table-column>
                <el-table-column prop="review" label="审核人" show-overflow-tooltip></el-table-column>
                <el-table-column prop="status" label="申请状态">
                    <template slot-scope="scope">
                        <el-button type="success" icon="el-icon-check" circle size="mini" v-if="scope.row.status==1"></el-button>
                        <el-button type="danger" icon="el-icon-close" circle v-else size="mini"></el-button>
                    </template>
                </el-table-column>
                <el-table-column prop="pass_time" label="通过时间" show-overflow-tooltip></el-table-column>
            </el-table>
        </div> 
        <div class="ui-list ui-box" id="reward_box">
            <div class="box-header ui-list-header">
                <div>奖惩情况</div>
                <div>
                    <el-button type="primary" size="mini">导出</el-button>
                </div>
            </div>
            <el-table :data="rewardList" :cell-style="cellStyle" :header-cell-style="rowClass">
                <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column prop="name" label="姓名"></el-table-column>
                <el-table-column prop="reward_status" label="奖励/惩罚" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span v-if="scope.row.reward_status==1" class="reward_status">奖励</span>
                        <span v-else class="reward_status" style="color:red;">惩罚</span>
                    </template>
                </el-table-column>
                <el-table-column prop="deal_time" label="处理时间" show-overflow-tooltip></el-table-column>
                
                <el-table-column prop="desc" label="原因" show-overflow-tooltip></el-table-column>
            </el-table>
        </div> 
        <div class="ui-list ui-box" id="person_box">
            <div class="box-header ui-list-header">
                <div>个人信息</div>
                <div>
                    <el-button type="primary" size="mini">导出</el-button>
                    <el-button type="primary" size="mini" @click="changePwdFlag=true">修改密码</el-button>
                </div>
            </div>
            <el-form class="ui-search-form"  label-width="120px" :disabled="formStatus">
                <el-form-item label="用户名" prop="username">
                    <el-input v-model="personInfo.username" class="ui-item-reset" size="small"></el-input>
                </el-form-item>
                <el-form-item label="手机号" prop="phone">
                    <el-input v-model="personInfo.phone" class="ui-item-reset" size="small"></el-input>
                </el-form-item>
                <el-form-item label="邮箱" prop="email">
                    <el-input v-model="personInfo.email" class="ui-item-reset" size="small"></el-input>
                </el-form-item>
                <el-form-item label="年龄">
                    <el-input v-model="personInfo.age" class="ui-item-reset" size="small"></el-input>
                </el-form-item>
                <el-form-item label="所属部门">
                    <el-input v-model="myDepartment.name" class="ui-item-reset" disabled="" size="small"></el-input>
                </el-form-item>
                <el-form-item label="性别" >
                    <!-- <el-input v-model="personInfo.sex" class="form_item_inpust" size="small"></el-input> -->
                    <el-radio v-model="personInfo.sex" :label="1" border size="small" class="item_radio">男</el-radio>
                    <el-radio v-model="personInfo.sex" :label="2" border size="small" class="item_radio">女</el-radio>
                </el-form-item>
            </el-form>
            <div class="ui-detail-btus">
                <el-button size="mini" @click="formStatus=false" v-if="formStatus">编辑</el-button>
                <el-button size="mini" @click="formStatus=true" v-else>取消</el-button>
                <el-button type="primary" size="mini" @click="editPerson">保存</el-button>
            </div>
        </div> 
        <el-dialog title="申请请假" :visible.sync="leaveDialogVisible" width="40%">
            <div class="ui-box leave_dialog">
                <el-form :model="leave" class="left_form" label-width="80px">
                    <el-form-item label="开始时间">
                        <el-date-picker v-model="leave.startTime" type="datetime" placeholder="选择开始时间" size="mini" class="leave_item_reset"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="开始时间">
                        <el-date-picker v-model="leave.endTime" type="datetime" placeholder="选择结束时间" size="mini" class="leave_item_reset"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="请假原由">
                        <el-select placeholder="请假原由" size="mini" class="leave_item_reset" v-model="leave.reason">
                            <el-option label="事假" value="1"></el-option>
                            <el-option label="病假" value="2"></el-option>
                            <el-option label="其他" value="2"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="原因备注">
                        <el-input type="textarea" v-model="leave.desc" class="leave_item_reset"></el-input>
                    </el-form-item>
                </el-form>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="leaveDialogVisible = false" size="mini">取 消</el-button>
                <el-button type="primary" @click="leaveDialogVisible = false" size="mini">确 定</el-button>
            </span>
        </el-dialog>
        <el-dialog title="修改密码" :visible.sync="changePwdFlag" width="400px">
            <el-form class="ui-search-form"  label-width="120px">
                <el-form-item label="旧密码">
                    <el-input size="mini" class="ui-item-reset" v-model="chaPwd.oldPassword"></el-input>
                </el-form-item>
                <el-form-item label="新密码">
                    <el-input size="mini" class="ui-item-reset" v-model="chaPwd.newPassword"></el-input>
                </el-form-item>
                <el-form-item label="新密码">
                    <el-input size="mini" class="ui-item-reset" v-model="chaPwd.confirmNewPassword" placeholder="请再次输入新密码"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="changePwdFlag = false" size="mini">取 消</el-button>
                <el-button type="primary" @click="changePwd" size="mini">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
export default {
  data(){
    return{
        personInfo: {
            username:'',
            password:'',
            rePassword:'',
            salt: '',
            email:'',
            phone:'',
            sex: '1',
            age: '',
            status: '1',
        },
      leave: {  //请假参数
          startTime: '',
          endTime: '',
          reason: '1',
          desc: '',
      },
      leaveList: [  //请假历史
          {name: '张子涵',create_time: '2018-06-19 12:49:30',start_time: '2018-06-29 08:49:30',end_time:'2018-06-30 19:49:30',
          pass_time: '2018-06-20 12:49:30',status: '0',review: '陈丹'},
          {name: '张子涵',create_time: '2018-05-19 12:49:30',start_time: '2018-05-29 08:49:30',end_time:'2018-05-30 19:49:30',
          pass_time: '2018-05-20 12:49:30',status: '1',review: '陈丹'},
      ],
      rewardList: [ //奖惩历史
          {name: '张子涵',reward_status: '1',deal_time: '2018-06-19 12:49:30',desc:'超额完成任务'},
          {name: '张子涵',reward_status: '0',deal_time: '2018-05-19 12:49:30',desc:'未完成任务'}
      ],
      title: this.$route.query.title,
      leaveDialogVisible: false,
      option_punch: {},
      option_mission: {},
      test:'',
      formStatus: true,
      changePwdFlag: false,
      chaPwd:{
        confirmNewPassword: '',
        newPassword: '',
        oldPassword: ''
      },
      myDepartment:{},
    }
  },
  mounted(){
      this.echart_init()
      this.getPerson()
  },
  methods:{
    back(){
      console.log(this.$route.query.title);
      this.$router.back(-1)
    },
    editPerson(){
        let users=this.personInfo
        delete users.createTime
        delete users.updateTime
        delete users.lastLoginTime
        console.log('user',users)
        post(`/pms_api/user/edit`,users).then(res => {
            this.cancelEdit()
        }).catch(err => {
            console.log(err)
        })
    },
    getPerson(){
        get(`/pms_api/user/detail`).then(res => {
            this.personInfo=res.data.rows[0]
            this.myDepartment=res.data.hasDepartment[0]
        }).catch(err => {
            console.log(err)
        })
    },
    changePwd(){
        post(`/pms_api/user/changePassword`,this.chaPwd).then(res => {
            this.changePwdFlag=false
        }).catch(err => {
            this.changePwdFlag=false
            console.log(err)
        })
    },
    toDiv(val){
        window.location.hash=val
    },
    notifyNone(){
        this.$notify({
            message:'对不起，此功能暂未完成',
            type: 'warning',
            position: 'top-right',
            duration: 1500,
        })
    },
    echart_init(){
        this.option_punch={
            title: {
                text: '我的打卡情况',
                left: 'center'
            },
            tooltip : {
                trigger: 'item',
                formatter: "{b}情况: {c} ({d}%)"
            },
            series: {
                type: 'pie',
                roseType: 'angle',
                data:[
                    { name: "请假", value: 5 },
                    { name: "异常", value: 2 },
                    { name: "正常", value: 15 }
                ]
            },
            backgroundColor: '#FFF',
            labelLine: {
                lineStyle: {
                    color: '#304156'
                }
            },
            textStyle: {
                color: '#304156'
            }
        }
        this.option_mission={
            title: {
                text: '小组任务完成情况',
                left: 'center',
            },
            legend: {
                data: ['项目一', '项目二'],
                // type: 'scroll',
                orient: 'vertical',
                right: 10,
                top: 20,
                bottom: 20,
            },
            textStyle: {
                color: '#304156'
            },
            radar: {
                indicator: [
                    {name: '张子涵',max: 100},{name: '陈峰强',max: 100},
                    {name: '曹林',max: 100},{name: '赵莹莹',max: 100},
                    {name: '李立强',max: 100},{name: '周阳',max: 100},
                ]
            },
            series: [{
                type:'radar',
                label:{
                    normal: {
                        show: true,
                    }
                },
                data: [
                    {value:[90,85,80,60,82,70],name:'项目一'},
                    {value:[70,50,68,53,72,58],name:'项目二'},
                ]
            }]
        }
        let chart1=this.$echarts.init(this.$refs.echart_punch)
        chart1.setOption(this.option_punch)
        let chart2=this.$echarts.init(this.$refs.echart_mission)
        chart2.setOption(this.option_mission)
    },
    //表格居中
    cellStyle({row, colum, rowIndex, columIndex}){
        return "text-align: center";
    },
    rowClass({row, rowIndex}){
        return "text-align: center";
    }
  }
}
</script>



